.container-img{
  border-radius: 0 10em 20em 0;
  border-right: 10px solid #A247FC;
  height: 35em;
  overflow: hidden;
}
.container-img img {
  float: left;
  margin-right: 20px;
  width: 31em;

}

.container-skills{
  width: 55em;
  height: 20em;
}

.skills h2 {
  margin: 0;
  font-size: 5em;
  text-align: center;
  margin-bottom: 60px;
  animation: tracking-in-expand-fwd 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

.skills ul {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  list-style: none;
  font-size: 30px;
  font-weight: bold;
  gap: 20px;
}

.skills li {
  margin-right: 10px;
}