.about-me h2 {
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
    color: #fff;
    -webkit-text-stroke: .5px #333;
}

.about-me h3 {
  font-size: 30px;
  font-weight: normal;
  margin-bottom: 20px;
  text-align: center;
  color: #c7c6c6;

  /* -webkit-text-stroke: .5px #333; */
}
