.video-presentation{
  width: 25em;
}

.about-me--texto{
  margin-top: 40px;
  font-size: 27px;
  color: #fff;
}
  
.btn-primary {
    display: inline-block;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: all 0.3s ease;
}
  
  .btn-primary:hover {
    background-color: #555;
  }