.container-project {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10em;
  height: 30em;
  color: #fff;
}

a{
  text-decoration: none;
  color: #fff;
}

a:not(:visited) {
 color: #fff;
}
.project {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 250px;
}


.project-img {
  width: 16em;
  margin-bottom: 10px;
  transition: .2s;
}

.project-img {
  transition: opacity 1s;
}

.animate {
  opacity: 1;
}
.project-img:hover{
  width: 16.4em;
  transition: .2s;
}

.project-title {
  font-size: 2.5em;
  font-weight: bold;
  
}